<template>
  <div class="bedusedlist">
    <v-row>
      <v-col cols="12" md="4">
        <h2 class="subheading grey--text">
          <v-icon class="mx-1">mdi-card-bulleted-outline</v-icon>
          <span>床位使用紀錄管理</span>
        </h2>
      </v-col>
      <v-col cols="12" md="8">
        <!-- 功能表 -->
        <v-toolbar dense>
          <v-autocomplete :items="sortItems" label="排序條件" v-model="sortByItem" hide-details return-object></v-autocomplete>
          <v-text-field
            hide-details
            prepend-icon="mdi-magnify"
            single-line
            label="搜尋"
            v-model="searchKey"
          ></v-text-field>
          
          <v-text-field type="date" label="開始時間" hide-details class="mx-1" v-model="searchStartDay"></v-text-field>
          <v-text-field type="date" label="結束時間" hide-details class="mx-1" v-model="searchEndDay"></v-text-field>
          <v-checkbox label="入住中" v-model="living" hide-details></v-checkbox>
          
          <popupadd @getAllData="getAllData" ref="editProcess" v-model="edititem" v-show="false"></popupadd>
                   
        </v-toolbar>
      </v-col>
    </v-row>

    <!-- 表單內容 -->
    <v-card>
      <v-card-title>
        <v-spacer></v-spacer>
        <v-btn class="mx-2" @click.stop="goBed">顯示床位管理</v-btn>
      </v-card-title>
      <v-card-text>
        <v-data-iterator
          :items="searchfilter"
          :items-per-page.sync="itemsPerPage"
          :footer-props="{ itemsPerPageOptions }"
          locale="zh-tw"
        >
          <template v-slot:default="props">
            <v-simple-table fixed-header class="mt-2 text-no-wrap" height="68vh">
              <template v-slot:default>
                <thead class="title">
                  <tr>
                    <th class="text-left"></th>
                    <th class="text-left">床號</th>
                    <th class="text-left">護字號</th>
                    <th class="text-left">姓名</th>
                    <th class="text-left">ID</th>
                    <th class="text-left">入住日期</th>
                    <th class="text-left">入住時間</th>
                    <th class="text-left">退床日期</th>
                    <th class="text-left">退床時間</th>
                    <th class="text-left">創建人員</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item,index) in props.items" :key="index">
                    <td>
                      <v-menu transition="scale-transition" offset-y>
                        <template v-slot:activator="{ on }">
                          <v-btn color="primary" dark v-on="on">功能</v-btn>
                        </template>
                        <v-list>
                          <v-list-item @click="edit(item)">
                            <v-list-item-title>修改</v-list-item-title>
                          </v-list-item>
                          <v-list-item @click="del(item)">
                            <v-list-item-title>刪除</v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>
                    </td>
                    <td>{{item.bname}}</td>
                    <td>{{item.p_code}}</td>
                    <td>{{item.uname}}</td>
                    <td>{{item.id_num}}</td>
                    <td>{{item.start_day}}</td>
                    <td>{{item.start_time}}</td>
                    <td>{{item.end_day | emptyDay}}</td>
                    <td>{{item.end_time | emptyTime}}</td>
                    <td>{{item.create_man}}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </template>
        </v-data-iterator>
      </v-card-text>
    </v-card>

    <pre>{{items[0]}}</pre>
  </div>
</template>

<script>
import qs from "qs";
import popupadd from "@/views/main/BedUsedAdd";
import dayjs from "dayjs";
import isBetween from "dayjs/plugin/isBetween";
dayjs.extend(isBetween);

export default {
  components: { popupadd },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      // viewData: [], //顯示的資料
      searchKey: "",
      searchStartDay:"",  //搜尋開始時間
      searchEndDay:"", ////搜尋結束時間
      living:false,  //入住中判斷
      sortByItem:{text:'入住日期',sortKey:'start_day'},
      sortItems : [
        {text:'床位',sortKey:'bname'},
        {text:'護字號',sortKey:'p_code'},
        {text:'入住日期',sortKey:'start_day'},
        {text:'退床日期',sortKey:'end_day'},
      ],
      edititem: {}, //修改中資料
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
    };
  },

  computed:{
    searchfilter() {
      var keys = this.searchKey.split(" ");
      var str = "";
      var match = [];

      if (this.living){
        //判斷入住中
        match = this.items.filter(i=>{
        return (
           dayjs(i.end_day).isBetween(this.searchStartDay, dayjs(this.searchEndDay), null, '[]')
        || (i.end_day == "0000-00-00" && dayjs(i.start_day) < dayjs(this.searchEndDay) )          
        )
        });
      }else{
        //判斷入住日或退住日
        match = this.items.filter(i=>{
        return (
           dayjs(i.start_day).isBetween(this.searchStartDay, dayjs(this.searchEndDay), null, '[]')
        || dayjs(i.end_day).isBetween(this.searchStartDay, dayjs(this.searchEndDay), null, '[]')  
        )
      });
      }
      

      match = keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, match);

      //排序依據
      match = match.sort((a,b)=>{
        return a[this.sortByItem.sortKey] > b[this.sortByItem.sortKey] ? 1 : -1;
      })
      //日期的話要取反向順序
      if (this.sortByItem.text.includes("日期")){
        match = match.reverse();
      }
  
      return match;
    },
  },

  created() {
    this.getAllData();
  },

  filters:{
    emptyDay(val){
      if (val == '0000-00-00'){
        return '';
      }else{
        return val;
      }
    },
    emptyTime(val){
      if (val == '00:00:00'){
        return '';
      }else{
        return val;
      }
    },
  },

  methods: {
    getAllData() {
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料
      this.axios
        .post("bed_used/getAll/" + this.$store.state.databaseName + "/bed_used")
        .then(rs => {
          // console.log(rs.data)
          this.items = rs.data;
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    edit(item) {
      //修改功能
      this.edititem = item;
      this.$refs.editProcess.$emit("fromParent");
    },
    del(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then(res => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey:delData.snkey,
          tablename:'bed_used',
          info: JSON.stringify(delData),
        };

        if (res) {
          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/bed_used",
              qs.stringify(postData)
            )
            .then(rs => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
    // search() {
    //   var searchArr = this.serachKey.split(" ");
    //   var outputitems = [...this.items];
    //   var str = "";
    //   searchArr.forEach(key => {
    //     if (key != "") {
    //       outputitems = outputitems.filter(item => {
    //         str = JSON.stringify(item).toUpperCase();
    //         if (str.includes(key.toUpperCase())) {
    //           return item;
    //         }
    //       });
    //     }
    //   });
    //   this.viewData = outputitems;
    // },
    goBed() {
      this.$router.push("/Main/bedlist");
    }
  }
};
</script>


<style lang="scss">
.bedusedlist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>